@media only screen and (max-width: 992px) {

  .homepage,
  .subpage {

    header {

      nav {

        &.navbar-default {

          &.navbar {
            position: static;

            ul {

              &.nav {

                &> li {
                  height:auto;
                  min-height:48px;
                  overflow: hidden;
                  width:100%;

                  &> a {
                    color: $white;

                    &:hover,
                    &:focus,
                    &:active {
                      background-color: $primaryBlueGrey;
                      color: $primaryYellow;
                    }
                  }

                  ul {
                    left:0;
                    width:100%;
                    transform: none;
                    padding: 20px 0 0 30px;
                    background-color: $primaryBlueGrey;
                    border: none;

                    &.dropdown-menu {
                      position: static;
                      display: block;

                      &:before {
                        content: none;
                      }

                      &> li {

                        &> a {
                          color: $white;
                          @include font-size(24);
                          font-weight: $light;

                          &:hover,
                          &:focus,
                          &:active {
                            background-color: $primaryBlueGrey;
                            color: $primaryYellow;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .navbar-header {

          button[aria-expanded="false"] {

            &:hover,
            &:focus,
            &:active {

              span {
                background-color: $primaryBlueGrey;
              }
            }
          }

          button[aria-expanded="true"] {

            span {
              background-color: $white;
            }
          }

          .navbar-toggle {
            display: block;
            position: absolute;
            top:10px;
            right:10px;
            z-index: 4;
            background-color: transparent;
            border:none;
          }
        }

        .navbar-collapse {
          background-color: $primaryBlueGrey;
          overflow: hidden;

          &.collapse {

            &:not(.in) {
              display: none!important;
            }
          }
        }

        ul {

          &.navbar-nav {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 3;
            width: 100%;
            min-height: 550px;
            background-color: $primaryBlueGrey;
            margin:0;
            padding: 60px 0 0 0;

            &> li{
              height:40px;
              border:none;

              a {

              }
            }
          }
        }
      }
    }
  }

  .homepage {

    header {

      nav {

        ul {

          &.navbar-nav {
            top:-36px;
          }
        }
      }
    }

    .navbar-default .navbar-toggle .icon-bar {
      background-color: $white;
    }
  }

}

